import { Component } from '@angular/core';
import { NavController, NavParams, AlertController, Alert } from 'ionic-angular';
import { Car } from '../../domain/car/car';
import { Booking } from '../../domain/booking/booking';


import { Http } from '@angular/http';
import { HomePage } from '../home/home';

@Component({
  selector: 'page-register',
  templateUrl: 'register.html'
})

export class RegisterPage {

  public booking: Booking;
  private _alert: Alert;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private _http: Http,
    private _alertCtrl: AlertController) {

    this.booking = new Booking(
      this.navParams.get('car'),
      this.navParams.get('totalPrice')
    );

    this._alert = _alertCtrl.create({
      title: 'Notice',
      buttons: [{text: 'Ok', handler: () => navCtrl.setRoot(HomePage)}]
    })
  }

  going(): void {
    this._http
      .get(`https://aluracar.herokuapp.com/salvarpedido?carro=${this.booking.car.nome}&nome=${this.booking.name}&preco=${this.booking.value}&endereco=${this.booking.address}&email=${this.booking.email}&dataAgendamento=${this.booking.date}`)
      .toPromise()
      .then(() => {
        this._alert.setSubTitle('Sucess!!');
        this._alert.present();
      })
      .catch(err => {
        console.error(err);
        this._alert.setSubTitle('Error!!');
        this._alert.present();
      });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad RegisterPagePage');
  }

}



